body {
  margin: 0;
  padding: 0;
  background: #e4e4e4;
}
.App {
  
}
.Banner {
  width: 100%;
  padding: 0;
  background: #525252;
  height: 50px;
}

.Banner .title {
  width: calc(50% - 80px);
  padding: 13px 0px 13px 40px;
  float: left;
  color: #ffffff;
  font-family: 'Roboto Condensed';
  font-size: 20px;
}
.Board {
  position: absolute;
  top: 50px;
  height: calc(100% - 50px);
  width: 100%;
  background: #ffffff;
}
